.header {
  width: 100%;
  height: 5rem;
  padding: 0 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #e2d5d5;
}

.logo {
  font-size: 20px;
  color: rgb(100, 10, 10);
}

.nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav li {
  margin-right: 0.5rem;
}

.nav a {
  text-decoration: none;
  color: rgb(160, 18, 18);
  font-size: 14px;
}

.nav a:hover, .nav a:active, .nav a.active {
  color: rgb(100, 10, 10);
}

.logoutbutton {
  border: none;
  color: rgb(160, 18, 18);
  background-color: transparent;
  font: 14px "Noto Sans JP", sans-serif;
  padding: 0px !important;
}

.logoutbutton:hover {
  background-color: transparent;
  color: rgb(100, 10, 10);
}

@media (min-width: 600px) {
  .logo {
    font-size: 2rem;
    color: rgb(100, 10, 10);
  }
  .nav li {
    margin-right: 2rem;
  }
  .nav a {
    font-size: 16px;
  }
  .logoutbutton {
    font: 16px "Noto Sans JP", sans-serif;
  }
}